* {
    margin: 0;
    padding: 0;
    font-family: cursive;
  }
  
  body {
    /* background-color: #020631; */
    height: 100vh;
    background: rgb(86,11,98);
    background: linear-gradient(90deg, rgba(86,11,98,0.9867297260701156) -5%, rgba(225,53,221,1) 100%, rgba(225,53,221,1) 100%, rgba(86,11,98,1) 100%)
  }
  
  .app {
    position: absolute;
    top: 8%;
    left: 50%;
    transform: translate(-50%, -10%);
  }
  
  .app .mainHeading h1, h3{
    text-align: center;
  }
  
  .app h1,
  h2 ,h3,h4,h5{
    color: white;
    text-align: center;
  }
  
  .app .input {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .app .input {
    margin-top: 0.8em;
    width: 21em;
    height: 2em;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 6px;
    padding: 5px;
  }
  
  .app .input input {
    width: 100%;
    height: 1.6em;
    border-color: transparent;
    outline-color: transparent;
  }
  
  .app i {
    cursor: pointer;
    font-size: larger;
    font-weight: 900;
    color: grey;
    margin-right: 5px;
  }
  .app .todos {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .app .todos .todo {
    margin-top: 0.8em;
    width: 20em;
    height: 2em;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 6px;
    padding: 5px;
  }
  .app .todos .todo .left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  .app .todos .todo .left p {
    margin-left: 3px;
    font-size: medium;
    font-weight: 700;
    font-style: normal;
    color: grey;
  }